/**
 * Converts CSV data to DHIS2 Tracker Event JSON format
 *
 * This module handles conversion of CSV files with custom columns to the format
 * expected by the DHIS2 Tracker API for event import.
 */

/**
 * Parse CSV line respecting quotes and commas
 * @param {string} line - A single line from CSV
 * @returns {string[]} Array of parsed values
 */
const parseCSVLine = line => {
  const result = [];
  let current = '';
  let inQuotes = false;
  for (let i = 0; i < line.length; i++) {
    const char = line[i];
    if (char === '"') {
      if (inQuotes && line[i + 1] === '"') {
        current += '"';
        i++;
      } else {
        inQuotes = !inQuotes;
      }
    } else if (char === ',' && !inQuotes) {
      result.push(current.trim());
      current = '';
    } else {
      current += char;
    }
  }
  result.push(current.trim());
  return result;
};

/**
 * Parse date string to ISO 8601 format
 * @param {string} dateString - Date string in various formats (M/D/YYYY, etc.)
 * @returns {string} ISO 8601 formatted date (YYYY-MM-DD)
 */
const parseDateToISO = dateString => {
  if (!dateString) {
    // Return DHIS2-compliant ISO 8601 format with time
    return new Date().toISOString();
  }
  try {
    // Handle D/M/YYYY or M/D/YYYY format
    const parts = dateString.split('/');
    if (parts.length === 3) {
      let day, month;

      // Parse the parts as numbers
      const part1 = parseInt(parts[0], 10);
      const part2 = parseInt(parts[1], 10);
      const year = parts[2];

      // Determine if it's D/M/YYYY or M/D/YYYY
      // If first part > 12, it must be day (D/M/YYYY format)
      // If second part > 12, it must be day (M/D/YYYY format)
      if (part1 > 12) {
        // D/M/YYYY format
        day = part1;
        month = part2;
      } else if (part2 > 12) {
        // M/D/YYYY format
        month = part1;
        day = part2;
      } else {
        // Ambiguous - assume D/M/YYYY (international standard)
        day = part1;
        month = part2;
      }

      // Validate month and day ranges
      if (month < 1 || month > 12 || day < 1 || day > 31) {
        throw new Error(`Invalid date components: day=${day}, month=${month}, year=${year}`);
      }

      // Pad with zeros
      const monthStr = month.toString().padStart(2, '0');
      const dayStr = day.toString().padStart(2, '0');

      // Validate the date is actually valid (e.g., not Feb 30, Nov 31, etc.)
      const testDate = new Date(Date.UTC(parseInt(year, 10), month - 1, day, 0, 0, 0, 0));
      // Check if the date components match what we input (Date auto-corrects invalid dates)
      if (testDate.getUTCFullYear() === parseInt(year, 10) && testDate.getUTCMonth() === month - 1 && testDate.getUTCDate() === day) {
        return `${year}-${monthStr}-${dayStr}T00:00:00.000Z`;
      } else {
        console.warn(`Invalid date detected: ${day}/${month}/${year} - skipping to fallback parsing`);
      }
    }

    // Fallback to Date parsing
    const date = new Date(dateString);
    if (!isNaN(date.getTime())) {
      return date.toISOString();
    }
  } catch (error) {
    console.error('Error parsing date:', dateString, error);
  }

  // Return current date as fallback (with time!)
  return new Date().toISOString();
};

/**
 * Create data element mapping from CSV headers
 * @param {string[]} headers - CSV column headers
 * @param {Object} dataElementMap - Map of column names to data element IDs
 * @returns {Object} Mapping of column index to data element info
 */
const createColumnMapping = (headers, dataElementMap) => {
  const mapping = {};
  headers.forEach((header, index) => {
    const normalizedHeader = header.trim();

    // Map special columns (not data elements)
    if (normalizedHeader.toLowerCase() === 'date') {
      mapping[index] = {
        type: 'date',
        name: normalizedHeader
      };
    } else if (normalizedHeader.toLowerCase() === 'region') {
      mapping[index] = {
        type: 'region',
        name: normalizedHeader
      };
    } else if (normalizedHeader.toLowerCase() === 'district') {
      mapping[index] = {
        type: 'district',
        name: normalizedHeader
      };
    } else if (normalizedHeader.toLowerCase().includes('chps')) {
      mapping[index] = {
        type: 'chps',
        name: normalizedHeader
      };
    } else if (dataElementMap[normalizedHeader]) {
      // This is a data element
      mapping[index] = {
        type: 'dataElement',
        name: normalizedHeader,
        id: dataElementMap[normalizedHeader]
      };
    } else {
      // Unknown column, treat as potential data element
      mapping[index] = {
        type: 'unknown',
        name: normalizedHeader
      };
    }
  });
  return mapping;
};

/**
 * Convert a CSV row to a DHIS2 tracker event
 * @param {string[]} rowValues - Values from a CSV row
 * @param {Object} columnMapping - Mapping of columns to data elements
 * @param {string} programId - DHIS2 Program ID
 * @param {string} programStageId - DHIS2 Program Stage ID
 * @param {string} orgUnitId - DHIS2 Organisation Unit ID
 * @returns {Object} DHIS2 tracker event object
 */
const convertRowToEvent = ({
  rowValues,
  columnMapping,
  programId,
  programStageId,
  orgUnitId
}) => {
  const dataValues = [];
  let occurredAt = new Date().toISOString();

  // Process each column value
  rowValues.forEach((value, index) => {
    const column = columnMapping[index];
    if (!column) {
      return;
    }
    const trimmedValue = value.trim();
    if (!trimmedValue) {
      return; // Skip empty values
    }
    if (column.type === 'date') {
      occurredAt = parseDateToISO(trimmedValue);
    } else if (column.type === 'dataElement' && column.id) {
      // Add as data value
      dataValues.push({
        dataElement: column.id,
        value: trimmedValue
      });
    }
    // region, district, chps are informational and don't become data values
    // unless they have corresponding data elements
  });

  // Create the event object
  const event = {
    program: programId,
    programStage: programStageId,
    orgUnit: orgUnitId,
    occurredAt: occurredAt,
    status: 'COMPLETED',
    dataValues: dataValues
  };
  return event;
};

/**
 * Main function to convert CSV data to DHIS2 Tracker format
 *
 * Mapping of form fields to DHIS2 entities:
 * - projectArea = DHIS2 Program ID (the actual program)
 * - programmeArea = Organisation Unit ID (Level 3 org unit)
 * - thematicArea = Level 2 Organisation Unit (parent of programmeArea)
 *
 * @param {string} csvText - Raw CSV text content
 * @param {Object} formData - Form data containing hierarchy selections
 * @param {string} formData.projectArea - DHIS2 Program ID
 * @param {string} formData.programmeArea - Organisation Unit ID (Level 3)
 * @param {string} formData.thematicArea - Level 2 Organisation Unit
 * @param {Object} dataElementMap - Map of CSV column names to DHIS2 data element IDs
 * @param {string} programStageId - DHIS2 Program Stage ID
 * @returns {Object} DHIS2 Tracker import payload
 */
export const convertCsvToTrackerFormat = ({
  csvText,
  formData,
  dataElementMap,
  programStageId
}) => {
  const {
    projectArea,
    programmeArea,
    thematicArea
  } = formData;
  if (!projectArea || !programmeArea || !thematicArea) {
    throw new Error('Project Area (Program), Programme Area (Org Unit), and Thematic Area are required');
  }
  if (!dataElementMap || Object.keys(dataElementMap).length === 0) {
    throw new Error('Data element mapping is required');
  }
  if (!programStageId) {
    throw new Error('Program Stage ID is required');
  }

  // Parse CSV
  const lines = csvText.split('\n').filter(line => line.trim());
  if (lines.length < 2) {
    throw new Error('CSV file must contain headers and at least one data row');
  }
  const headers = parseCSVLine(lines[0]);
  const columnMapping = createColumnMapping(headers, dataElementMap);

  // Convert data rows to events
  const events = [];
  for (let i = 1; i < lines.length; i++) {
    const line = lines[i].trim();
    if (!line) {
      continue;
    }
    try {
      const rowValues = parseCSVLine(line);

      // Correct mapping:
      // projectArea = Program ID (DHIS2 Program)
      // programmeArea = Org Unit ID (Level 3 Organisation Unit)
      const event = convertRowToEvent({
        rowValues,
        columnMapping,
        programId: projectArea,
        programStageId,
        orgUnitId: programmeArea
      });

      // Only add events with data values
      if (event.dataValues && event.dataValues.length > 0) {
        events.push(event);
      }
    } catch (error) {
      console.error(`Error processing row ${i + 1}:`, error);
      // Continue processing other rows
    }
  }
  if (events.length === 0) {
    throw new Error('No valid events were created from the CSV data');
  }

  // Create the DHIS2 Tracker import payload
  const trackerPayload = {
    events: events
  };
  return trackerPayload;
};

/**
 * Convert the tracker payload to a File/Blob for upload
 * @param {Object} trackerPayload - DHIS2 Tracker import payload
 * @returns {Blob} Blob containing the JSON payload
 */
export const convertPayloadToFile = trackerPayload => {
  const jsonString = JSON.stringify(trackerPayload, null, 2);
  return new Blob([jsonString], {
    type: 'application/json'
  });
};

/**
 * Helper function to get data element IDs from CSV headers
 * This is used to determine which data elements need to be created
 * @param {string} csvText - Raw CSV text content
 * @returns {string[]} Array of unique column names (potential data elements)
 */
export const extractDataElementNamesFromCsv = csvText => {
  const lines = csvText.split('\n').filter(line => line.trim());
  if (lines.length < 1) {
    return [];
  }
  const headers = parseCSVLine(lines[0]);

  // Filter out known non-data-element columns
  const nonDataElementColumns = ['date', 'region', 'district', 'chps zone', 'chps', 'where talking book was used', 'language of content played'];
  return headers.map(h => h.trim()).filter(h => {
    const lower = h.toLowerCase();
    return !nonDataElementColumns.some(nde => lower.includes(nde.toLowerCase()));
  }).filter(h => h.length > 0);
};