// Function to convert period format to a standardized readable format
const formatPeriod = period => {
  if (!period) {
    return period;
  }

  // Handle YYYYMMDD format (8 digits)
  if (period.length === 8 && /^\d{8}$/.test(period)) {
    const year = period.substring(0, 4);
    const month = period.substring(4, 6);
    const day = period.substring(6, 8);
    const monthNames = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
    const monthIndex = parseInt(month, 10) - 1;
    if (monthIndex >= 0 && monthIndex < 12) {
      return `${monthNames[monthIndex]} ${parseInt(day, 10)}, ${year}`;
    }
  }

  // Handle YYYYMM format (6 digits)
  if (period.length === 6 && /^\d{6}$/.test(period)) {
    const year = period.substring(0, 4);
    const month = period.substring(4, 6);
    const monthNames = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
    const monthIndex = parseInt(month, 10) - 1;
    if (monthIndex >= 0 && monthIndex < 12) {
      return `${monthNames[monthIndex]} ${year}`;
    }
  }
  return period;
};

// Function to transform data values into pivot table format
const transformDataValuesToPivot = (dataValues, dataElementsLookup, orgUnitsLookup) => {
  if (!dataValues || !Array.isArray(dataValues)) {
    return {
      pivotData: [],
      dataElements: [],
      periods: []
    };
  }

  // Create maps to store data: period -> dataElement -> {value, storedBy, lastUpdated, orgUnit}
  const dataMap = new Map();
  const dataElementsSet = new Set();
  const periodsSet = new Set();

  // Process each data value
  dataValues.forEach(dataValue => {
    const dataElementName = dataElementsLookup[dataValue.dataElement] || dataValue.dataElement;
    const formattedPeriod = formatPeriod(dataValue.period);
    const orgUnitName = orgUnitsLookup[dataValue.orgUnit] || dataValue.orgUnit;
    dataElementsSet.add(dataElementName);
    periodsSet.add(formattedPeriod);
    if (!dataMap.has(formattedPeriod)) {
      dataMap.set(formattedPeriod, new Map());
    }
    dataMap.get(formattedPeriod).set(dataElementName, {
      value: dataValue.value,
      storedBy: dataValue.storedBy,
      lastUpdated: dataValue.lastUpdated,
      program: orgUnitName
    });
  });

  // Convert sets to sorted arrays
  const dataElements = Array.from(dataElementsSet).sort();
  const periods = Array.from(periodsSet).sort();

  // Create pivot table data with metadata columns
  const pivotData = periods.map(period => {
    const periodMap = dataMap.get(period);
    const firstDataEntry = periodMap ? Array.from(periodMap.values())[0] : null;
    const row = {
      Period: period,
      Program: (firstDataEntry === null || firstDataEntry === void 0 ? void 0 : firstDataEntry.program) || '',
      StoredBy: (firstDataEntry === null || firstDataEntry === void 0 ? void 0 : firstDataEntry.storedBy) || '',
      LastUpdated: (firstDataEntry === null || firstDataEntry === void 0 ? void 0 : firstDataEntry.lastUpdated) || ''
    };
    dataElements.forEach(dataElement => {
      const dataEntry = periodMap && periodMap.has(dataElement) ? periodMap.get(dataElement) : null;
      row[dataElement] = dataEntry ? dataEntry.value : '';
    });
    return row;
  });
  return {
    pivotData,
    dataElements,
    periods
  };
};

// Function to transform data values to original flat format (for non-CSV formats)
const transformDataValuesFlat = (dataValues, dataElementsLookup, orgUnitsLookup) => {
  if (!dataValues || !Array.isArray(dataValues)) {
    return [];
  }
  return dataValues.map(dataValue => {
    const dataElementName = dataElementsLookup[dataValue.dataElement] || dataValue.dataElement;
    const orgUnitName = orgUnitsLookup[dataValue.orgUnit] || dataValue.orgUnit;
    const formattedPeriod = formatPeriod(dataValue.period);
    return {
      dataElement: dataElementName,
      period: formattedPeriod,
      program: orgUnitName,
      value: dataValue.value,
      storedBy: dataValue.storedBy,
      lastUpdated: dataValue.lastUpdated
    };
  });
};

